import React, { useState, useEffect, useCallback, useRef } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import Footer from "./Footer";
import Navbar from "./Navbar";
import pic1 from "./assets/img/banner-02.jpg";
import pic2 from "./assets/img/banner-2.jpg";
import pic3 from "./assets/img/specialities/specialities-01.png";
import pic4 from "./assets/img/specialities/specialities-02.png";
import pic5 from "./assets/img/specialities/specialities-03.png";
import pic6 from "./assets/img/specialities/specialities-04.png";
import pic7 from "./assets/img/specialities/specialities-05.png";
import feature1 from "./assets/img/features/feature.png";
import feature2 from "./assets/img/features/feature-01.jpg";
import feature3 from "./assets/img/features/feature-02.jpg";
import feature4 from "./assets/img/features/feature-02.jpg";
import feature5 from "./assets/img/features/feature-06.jpg";
import axios from "axios";
const Index = () => {
  const swiperRef = useRef(null);
  const [specilitiesData, setSpecilitiesData] = useState([]);
  useEffect(() => {
    async function getSpecialities() {
      const results = await axios(
        "https://ymp-api.herokuapp.com/api/specialities?limit=100&offset=0"
      );
      setSpecilitiesData(results.data);
    }
    getSpecialities();
  }, []);
  const [currentIndex, updateCurrentIndex] = useState(0);
  const params = {
    initialSlide: 2,
    pagination: {
      el: ".swiper-pagination",
      type: "bullets",
      clickable: true,
    },
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
    spaceBetween: 30,
    loop: true,
    autoplay: true,
  };

  // Manipulate swiper from outside
  const goNext = () => {
    if (swiperRef.current && swiperRef.current.swiper) {
      swiperRef.current.swiper.slideNext();
      updateIndex();
    }
  };
  const goNextSwiper = () => {
    document.getElementById("sliderTop1").style.backgroundColor = "#3297CB";
    document.getElementById("sliderTop2").style.backgroundColor = "grey";
    if (swiperRef.current && swiperRef.current.swiper) {
      swiperRef.current.swiper.slideNext();
      updateIndex();
    }
  };

  const goPrevSwiper = () => {
    document.getElementById("sliderTop2").style.backgroundColor = "#3297CB";
    document.getElementById("sliderTop1").style.backgroundColor = "grey";
    if (swiperRef.current && swiperRef.current.swiper) {
      swiperRef.current.swiper.slidePrev();
    }
  };
  let specilitiesDataList =
    specilitiesData && specilitiesData[0] ? (
      specilitiesData.map((p, i) => (
        <SwiperSlide key={p.id}>
          <div className="speicality-item text-center">
            <div className="speicality-img">
              <img src={p.image} className="img-fluid" alt="Speciality" />
              <span>
                <i className="fa fa-circle" aria-hidden="true" />
              </span>
            </div>
            <p>{p.name}</p>
          </div>
        </SwiperSlide>
      ))
    ) : (
      <></>
    );
  const goPrev = () => {
    if (swiperRef.current && swiperRef.current.swiper) {
      swiperRef.current.swiper.slidePrev();
    }
  };
  const updateIndex = useCallback(
    () => updateCurrentIndex(swiperRef.current.swiper.realIndex),
    []
  );
  // Add eventlisteners for swiper after initializing
  useEffect(() => {
    const swiperInstance = swiperRef.current.swiper;

    if (swiperInstance) {
      swiperInstance.on("slideChange", updateIndex);
    }

    return () => {
      if (swiperInstance) {
        swiperInstance.off("slideChange", updateIndex);
      }
    };
  }, [updateIndex, swiperRef]);

  // Fetch specilities Data Here

  return (
    <div>
      <div>
        <div className="main-wrapper">
          {/* Header */}
          <div id="header" />
          <Navbar />
          <div className="pharmacy-home-slider">
            <div className="swiper-container">
              <div className="swiper-wrapper">
                <Swiper
                  spaceBetween={20}
                  slidesPerView={1}
                  onSlideChange={() => console.log("slide change")}
                  onSwiper={(swiper) => console.log(swiper)}
                  {...params}
                  ref={swiperRef}
                >
                  <SwiperSlide>
                    <div className="swiper-slide">
                      <img src={pic1} alt="" style={{ width: "100%" }} />
                      <div className="banner-wrapper">
                        <div className="banner-header text-center">
                          <h1>Welcome to Your Medical Portal! - 2</h1>
                          <p>
                            Lorem ipsum dolor sit amet, consectetur adipiscing
                            elit, sed do eiusmod tempor incididunt ut labore et
                            dolore magna aliqua.
                          </p>
                        </div>
                      </div>
                    </div>
                  </SwiperSlide>
                  <SwiperSlide>
                    <div className="swiper-slide">
                      <img src={pic2} alt="" style={{ width: "100%" }} />
                      <div className="banner-wrapper">
                        <div className="banner-header text-center">
                          <h1>Welcome to Your Medical Portal! - 3</h1>
                          <p>
                            Lorem ipsum dolor sit amet, consectetur adipiscing
                            elit, sed do eiusmod tempor incididunt ut labore et
                            dolore magna aliqua.
                          </p>
                        </div>
                      </div>
                    </div>
                  </SwiperSlide>
                  <SwiperSlide>
                    <div className="swiper-slide">
                      <div class="swiper-slide">
                        <img src={pic1} alt="" style={{ width: "100%" }} />
                        <div class="banner-wrapper">
                          <div class="banner-header text-center">
                            <h1>Welcome to Your Medical Portal! - 1</h1>
                            <p>
                              Lorem ipsum dolor sit amet, consectetur adipiscing
                              elit, sed do eiusmod tempor incididunt ut labore
                              et dolore magna aliqua.
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </SwiperSlide>
                </Swiper>
              </div>

              <div className="swiper-button-next" onClick={goNext} />
              <div className="swiper-button-prev" onClick={goPrev} />
            </div>

            <div
              style={{
                textAlign: "center",
                alignItems: "center",
              }}
            >
              <div
                onClick={goNextSwiper}
                id="sliderTop1"
                style={{
                  display: "inline-block",
                  marginLeft: "auto",
                  marginRight: "auto",
                  marginTop: "3px",
                  height: "5px",
                  backgroundColor: "grey",
                  width: "20px",
                }}
              ></div>
              &nbsp;
              <div
                id="sliderTop2"
                onClick={goPrevSwiper}
                style={{
                  display: "inline-block",
                  marginLeft: "auto",
                  marginRight: "auto",
                  marginTop: "3px",
                  height: "5px",
                  backgroundColor: "grey",
                  width: "20px",
                }}
              ></div>
            </div>
          </div>

          <div className="section section-search">
            <div className="swiper-container">
              <div className="banner-wrapper">
                <div className="banner-header text-center">
                  <h1>Search Doctor, Make an Appointment</h1>
                  <p>
                    Discover the best doctors, clinic &amp; hospital the city
                    nearest to you.
                  </p>
                </div>
                {/* Search */}
                <div className="search-box">
                  {/* <form action="search.html"> */}
                  <form action="map-list.html">
                    <div className="form-group search-location mt-3">
                      <input
                        type="text"
                        className="form-control"
                        placeholder="Search Location"
                      />
                      <span className="form-text">Based on your Location</span>
                    </div>
                    <div className="form-group search-info mt-3">
                      <input
                        type="text"
                        className="form-control"
                        placeholder="Search Doctors, Clinics, Hospitals, Diseases Etc"
                      />
                      <span className="form-text">
                        Ex : Dental or Sugar Check up etc
                      </span>
                    </div>
                    <button
                      type="submit"
                      className="btn btn-primary search-btn mt-3"
                      style={{ background: "#1CC0FC" }}
                    >
                      <i className="fas fa-search" /> <span>Search</span>
                    </button>
                  </form>
                </div>
                {/* /Search */}
              </div>
            </div>
          </div>
          <section className="section section-specialities">
            <div className="container-fluid">
              <div className="section-header text-center">
                <h2>Clinic and Specialities</h2>
                <p className="sub-title">
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                  do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                </p>
              </div>
              <div className="row justify-content-center">
                <div className="col-md-9">
                  {/* Slider */}
                  <div className="specialities-slider slider">
                    <Swiper
                      spaceBetween={30}
                      slidesPerView={4}
                      onSlideChange={() => console.log("slide change")}
                      onSwiper={(swiper) => console.log(swiper)}
                    >
                      {specilitiesDataList}
                    </Swiper>
                  </div>
                </div>
              </div>
            </div>
          </section>
          <section
            className="section section-features"
            style={{ backgroundColor: "#F9F9F9" }}
          >
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-4 features-img">
                  <img src={feature1} className="img-fluid" alt="Feature" />
                </div>
                <div className="col-md-8">
                  <div className="section-header">
                    <h2 className="mt-2">What do we offer?</h2>
                    <p>
                      It is a long established fact that a reader will be
                      distracted by the readable content of a page when looking
                      at its layout.{" "}
                    </p>
                  </div>
                  <div className="features-slider slider">
                    <div className="row">
                      <div className=" col-sm-6 col-md-3">
                        <div className="feature-item text-center">
                          <img
                            src={feature2}
                            className="img-fluid"
                            alt="Feature"
                          />
                          <p>Medical</p>
                        </div>
                      </div>
                      <div className=" col-sm-6 col-md-3">
                        <div className="feature-item text-center">
                          <img
                            src={feature3}
                            className="img-fluid"
                            alt="Feature"
                          />
                          <p>Doctor Recommendation</p>
                        </div>
                      </div>
                      <div className=" col-sm-6 col-md-3">
                        <div className="feature-item text-center">
                          <img
                            src={feature4}
                            className="img-fluid"
                            alt="Feature"
                          />
                          <p>Service A</p>
                        </div>
                      </div>
                      <div className=" col-sm-6 col-md-3">
                        <div className="feature-item text-center">
                          <img
                            src={feature5}
                            className="img-fluid"
                            alt="Feature"
                          />
                          <p>Service B</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>

          <div id="footer" />
          <Footer />
        </div>
      </div>
    </div>
  );
};

export default Index;
