import React, { useState, useEffect } from "react";
import Navbar from "./Navbar";
import doctors1 from "./assets/img/doctors/doctor-thumb-01.jpg";
import Sidebar from "./Sidebar";
import { Modal, Button } from "react-bootstrap";
import { HTTP_DELETEHANDLER, HTTP_LISTHANDLER } from "..//..//http";
import axios from "axios";
const Specialities = () => {
  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [name, setName] = useState(null);
  const [add, setAddModal] = useState(false);
  const [edit, setEdit] = useState(false);
  const [deleteModal, setdeleteModal] = useState(false);
  const [specilitiesData, setSpecilitiesData] = useState([]);
  let [files, setFiles] = useState(null);
  let [imageName, setImageName] = useState(null);
  let [currentStatus, setCurrentStatus] = useState(null);
  let [pageNumber, setPageNumber] = useState(0);
  let [totalRecords, setTotalRecords] = useState(null);
  const [updateData, setUpdateData] = useState([]);
  const [deleteAbleRecordId, setdeletAbleRecordId] = useState(null);
  const setValueofOffset = () => {
    let offsetvalue = 0;
    setPageNumber(pageNumber + 1);
    if (offset == 0) {
      offsetvalue = (offset + 1) * limit - 1;
      setOffset((offset + 1) * limit - 1);
    } else {
      offsetvalue = offset + limit;
      setOffset(offset + limit);
    }
    listHandler(offsetvalue, limit);
  };
  const setValueofOffsetPrevious = () => {
    let offsetvalue = 0;
    setPageNumber(pageNumber - 1 <= 0 ? 0 : pageNumber - 1);
    if (offset == 0) {
      offsetvalue = 0;
      setOffset(0);
    } else {
      offsetvalue = offset - limit <= 0 ? 0 : offset - limit;
      setOffset(offset - limit <= 0 ? 0 : offset - limit);
    }
    listHandler(offsetvalue, limit);
  };

  const deleteModalHandler = (id) => {
    setdeletAbleRecordId(id);

    setdeleteModal(true);
  };
  const deleteHandler = (e) => {
    e.preventDefault();

    HTTP_DELETEHANDLER(deleteAbleRecordId)
      .then((r) => {
        if (200 === r.status) {
          console.log("deleted");
        }
      })
      .catch((e) => {
        //setMessage("Feedback submission failed, Please try again.");
      });
    setdeleteModal(false);
    window.location.reload();
  };
  const listHandler = (offsetvalue, limit) => {
    console.log("limit" + limit);
    console.log("offset" + offsetvalue);
    HTTP_LISTHANDLER(limit, offsetvalue)
      .then((r) => {
        if (200 === r.status) {
          setSpecilitiesData(r.data);
        }
      })
      .catch(() => {
        //setMessage("Feedback submission failed, Please try again.");
      });
  };
  const updateModalHandler = (data) => {
    setUpdateData(data);
    setName(data.name);
    setEdit(true);
  };
  const setLimiValue = (value) => {
    setLimit(value);
    listHandler(0, value);
  };

  const updateStatusHandler = (data) => {
    let statusValue = "";
    if (data.status == true) {
      statusValue = "NOTACTIVE";
    } else {
      statusValue = "ACTIVE";
    }
    console.log(currentStatus);
    updateStatusRecord(data.id, statusValue);
  };
  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);

      fileReader.onload = () => {
        resolve(fileReader.result);
      };

      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  };
  const uploadImage = async (e) => {
    const file = e.target.files[0];
    const base64 = await convertBase64(file);
    setFiles(base64);
    setImageName(file.name);
  };

  useEffect(() => {
    async function getSpecialities() {
      const results = await axios(
        "https://ymp-api.herokuapp.com/api/specialities?limit=" +
          limit +
          "&offset=" +
          offset
      );
      setSpecilitiesData(results.data);
      console.log(results.data);
    }
    async function getTotal() {
      const totalResults = await axios(
        "https://ymp-api.herokuapp.com/api/speciality/count"
      );
      setTotalRecords(totalResults.data);
      console.log(totalResults.data);
    }
    console.log("inside useEffect");
    getTotal();
    getSpecialities();
  }, []);
  const updateStatusRecord = (idss, statusValue) => {
    fetch(`https://ymp-api.herokuapp.com/api/specialities`, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        id: idss,
        name: name,
        image: files,
        status: statusValue,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        // Do some stuff ...
      })
      .catch((err) => console.log(err));
    setEdit(false);
    window.location.reload();
  };
  const createRecord = () => {
    fetch(`https://ymp-api.herokuapp.com/api/specialities`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        name: name,
        status: "ACTIVE",
        image: files,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        // Do some stuff ...
      })
      .catch((err) => console.log(err));
  };
  const updateRecord = () => {
    fetch(`https://ymp-api.herokuapp.com/api/specialities`, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        id: updateData.id,
        name: name,
        image: files,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        // Do some stuff ...
      })
      .catch((err) => console.log(err));
    setEdit(false);
    window.location.reload();
    listHandler(offset, limit);
  };

  let specilitiesDataList =
    specilitiesData && specilitiesData[0] ? (
      specilitiesData.map((p, i) => (
        <tr key={p.id}>
          <td>{p.specialityId}</td>
          <td>
            <h2 className="table-avatar">
              <a href="profile" className="avatar avatar-sm mr-2">
                <img className="avatar-img" src={p.image} alt="Speciality" />
              </a>
              <a href="profile">{p.name}</a>
            </h2>
          </td>
          <td>
            <div className="status-toggle">
              <input
                type="checkbox"
                id={"status_" + p.id}
                className="check"
                checked={p.status}
                value={p.status}
                onChange={() => updateStatusHandler(p)}
              />
              <label htmlFor={"status_" + p.id} className="checktoggle">
                checkbox
              </label>
            </div>
          </td>
          <td className="text-right">
            <div className="actions">
              <a
                onClick={() => updateModalHandler(p)}
                className="btn btn-sm bg-success-light"
                data-toggle="modal"
                href="#edit_specialities_details"
              >
                <i className="fe fe-pencil" /> Edit
              </a>
              <a
                onClick={() => deleteModalHandler(p.id)}
                data-toggle="modal"
                href="#delete_modal"
                className="btn btn-sm bg-danger-light"
              >
                <i className="fe fe-trash" /> Delete
              </a>
            </div>
          </td>
        </tr>
      ))
    ) : (
      <></>
    );

  return (
    <div>
      <div>
        <meta charSet="utf-8" />
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1.0, user-scalable=0"
        />
        <title>Your Medical Portal - Specialities Page</title>
        <div className="main-wrapper">
          <Navbar />
          <Sidebar />
          <div className="page-wrapper">
            <div className="content container-fluid">
              {/* Page Header */}
              <div className="page-header">
                <div className="row">
                  <div className="col-sm-7 col-auto">
                    <h3 className="page-title">Specialities</h3>
                    <ul className="breadcrumb">
                      <li className="breadcrumb-item">
                        <a href="index">Dashboard</a>
                      </li>
                      <li className="breadcrumb-item active">Specialities</li>
                    </ul>
                  </div>
                  <div className="col-sm-5 col">
                    <Button
                      onClick={() => setAddModal(true)}
                      variant="info"
                      className="btn btn-primary float-right mt-2"
                    >
                      Add
                    </Button>
                  </div>
                </div>
              </div>
              {/* /Page Header */}
              <div className="row">
                <div className="col-sm-12">
                  <div className="card">
                    <div className="card-body">
                      <div className="table-responsive">
                        <table className="datatable table table-hover table-center mb-0">
                          <thead>
                            <tr>
                              <td>
                                Show
                                <select
                                  value={limit}
                                  onChange={(e) => {
                                    setLimiValue(e.target.value);
                                  }}
                                >
                                  <option value="10">10</option>
                                  <option value="25">25</option>
                                  <option value="50">50</option>
                                  <option value="100">100</option>
                                </select>
                                Enteries
                              </td>
                              <td></td>
                              <td></td>
                              <td></td>
                            </tr>
                            <tr>
                              <th>#</th>
                              <th>Specialities</th>
                              <th>Status</th>
                              <th className="text-right">Actions</th>
                            </tr>
                          </thead>
                          <tbody>
                            {specilitiesDataList}
                            <tr>
                              <td>
                                {" "}
                                Showing {offset + 1} to{" "}
                                {parseInt(offset) + parseInt(limit) >
                                parseInt(totalRecords)
                                  ? totalRecords
                                  : parseInt(offset) + parseInt(limit)}{" "}
                                of {totalRecords} enteries
                              </td>
                              <td></td>
                              <td></td>
                              <td>
                                <Button
                                  onClick={() => setAddModal(true)}
                                  variant="info"
                                  className="btn btn-primary float-right mt-2"
                                  onClick={() => setValueofOffset()}
                                  style={{
                                    color: "black",
                                    backgroundColor: "white",
                                    borderColor: "black",
                                  }}
                                >
                                  Next
                                </Button>

                                <Button
                                  variant="info"
                                  className="float-right mt-2"
                                >
                                  {pageNumber}
                                </Button>
                                <Button
                                  variant="info"
                                  className="float-right mt-2"
                                  onClick={() => setValueofOffsetPrevious()}
                                  style={{
                                    color: "black",
                                    backgroundColor: "white",
                                    borderColor: "black",
                                  }}
                                >
                                  Previous
                                </Button>
                              </td>
                            </tr>
                            <tr></tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* /Page Wrapper */}

          <Modal
            size="lg"
            show={add}
            onHide={() => setAddModal(false)}
            aria-labelledby="example-modal-sizes-title-sm"
            centered
          >
            <Modal.Header closeButton>
              <div className="modal-header">
                <h5 className="modal-title">Add Specialities</h5>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <span aria-hidden="true"></span>
                </button>
              </div>
            </Modal.Header>
            <Modal.Body>
              <div className="modal-body">
                <form>
                  <div className="row form-row">
                    <div className="col-12 col-sm-6">
                      <div className="form-group">
                        <label>Specialities</label>
                        <input
                          type="text"
                          className="form-control"
                          value={name}
                          name="name"
                          onInput={(e) => setName(e.target.value)}
                        />
                      </div>
                    </div>
                    <div className="col-12 col-sm-6">
                      <div className="form-group">
                        <label>Image</label>

                        <input
                          className="form-control"
                          type="file"
                          onChange={(e) => {
                            uploadImage(e);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <button
                    onClick={createRecord}
                    type="submit"
                    className="btn btn-primary btn-block"
                  >
                    Save Changes
                  </button>
                </form>
              </div>
            </Modal.Body>
          </Modal>
          <Modal
            size="lg"
            show={edit}
            onHide={() => setEdit(false)}
            aria-labelledby="example-modal-sizes-title-sm"
            centered
          >
            <Modal.Header closeButton>
              <div className="modal-header">
                <h5 className="modal-title">Edit Specialities</h5>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <span aria-hidden="true"></span>
                </button>
              </div>
            </Modal.Header>
            <Modal.Body>
              <div className="modal-body">
                <form>
                  <div className="row form-row">
                    <div className="col-12 col-sm-6">
                      <div className="form-group">
                        <label>Specialities</label>

                        <input
                          type="text"
                          className="form-control"
                          value={name}
                          onInput={(e) => setName(e.target.value)}
                        />
                      </div>
                    </div>
                    <div className="col-12 col-sm-6">
                      <div className="form-group">
                        <label>Image</label>
                        <input
                          className="form-control"
                          type="file"
                          onChange={(e) => {
                            uploadImage(e);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <button
                    type="submit"
                    onClick={updateRecord}
                    className="btn btn-primary btn-block"
                  >
                    Save Changes
                  </button>
                </form>
              </div>
            </Modal.Body>
          </Modal>
          <Modal
            size="lg"
            show={deleteModal}
            onHide={() => setdeleteModal(false)}
            aria-labelledby="example-modal-sizes-title-sm"
            centered
          >
            <Modal.Body>
              <div className="modal-body">
                <div className="form-content p-2">
                  <h4 className="modal-title">Delete</h4>
                  <p className="mb-4">Are you sure want to delete?</p>
                  <button
                    type="button"
                    onClick={deleteHandler}
                    className="btn btn-primary"
                  >
                    Yes Delete{" "}
                  </button>
                  <button
                    type="button"
                    className="btn btn-danger"
                    data-dismiss="modal"
                    onClick={() => setdeleteModal(false)}
                  >
                    Close
                  </button>
                </div>
              </div>
            </Modal.Body>
          </Modal>
        </div>
      </div>
    </div>
  );
};

export default Specialities;
